<h3>值转换器</h3>
<p>组件的 value 与 各选择器的 value 的转换器。</p>
<h4>join</h4>
<p>默认为<code>'join'</code>，表示将 values 数组 join 之后变成 value。这种情况下 value 一般不会重复，推荐使用。</p>
