<h3>自动选择</h3>
<p>此功能默认开启，父级切换时会跟据当前级别的<code>value</code>自动选择，如果没有与<code>value</code>相匹配的项，会自动选择第一个显示且非禁用的项。</p>
<p>可以将<code>auto-select</code>设置为<code>false</code>，关闭此功能。</p>
