<h2>CountUp API</h2>
<h3>Props/Attrs</h3>
<table>
<thead>
<tr>
<th>Prop/Attr</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>start</td>
<td>Number</td>
<td><code>0</code></td>
<td>渐变开始数值</td>
</tr>
<tr>
<td>end</td>
<td>Number</td>
<td></td>
<td>渐变结束数值</td>
</tr>
<tr>
<td>duration</td>
<td>Number</td>
<td><code>2</code></td>
<td>渐变时间间隔，时间单位为秒</td>
</tr>
<tr>
<td>isEasing</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否使用 动画函数<code>easingFn</code>处理数据</td>
</tr>
<tr>
<td>isGroup</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否分组，默认分组分隔符<code>'separator'</code>属性值为<code>','</code></td>
</tr>
<tr>
<td>separator</td>
<td>String</td>
<td><code>','</code></td>
<td>分组分隔符</td>
</tr>
<tr>
<td>decimals</td>
<td>Number</td>
<td><code>0</code></td>
<td>小数点后几位</td>
</tr>
<tr>
<td>easingFn</td>
<td>Function</td>
<td><code>'easeOutExpo'</code></td>
<td>自定义渐变动画，默认使用 <code>Robert Penner's easeOutExpo</code>函数</td>
</tr>
<tr>
<td>simplify</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否使用自定义单位简化数据</td>
</tr>
<tr>
<td>unit</td>
<td>Array</td>
<td><code>[[3, 'K+'], [6, 'M+'], [9, 'G+']]</code></td>
<td>配合<code>'simplify'</code>属性使用的自定义单位</td>
</tr>
<tr>
<td>prefix</td>
<td>String</td>
<td><code>''</code></td>
<td>前缀内容</td>
</tr>
<tr>
<td>suffix</td>
<td>String</td>
<td><code>''</code></td>
<td>后缀内容</td>
</tr>
<tr>
<td>endCallback</td>
<td>Function</td>
<td>``</td>
<td>渐变结束后的回调函数</td>
</tr>
<tr>
<td>autoStart</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否开启渐变</td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<h4>(default)</h4>
<p>插入文本或 HTML。</p>
<h3>Events</h3>
<h4>@update</h4>
<p>更新渐变结束</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event</td>
<td>Number</td>
<td>最新结束值</td>
</tr>
</tbody>
</table>
<h4>@reset</h4>
<p>重置</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event</td>
<td>Number</td>
<td>初始值</td>
</tr>
</tbody>
</table>
<h4>@pauseResume</h4>
<p>切换渐变</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>-</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>Methods</h3>
<h4>update(value)</h4>
<p>更新渐变结束值</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>Number</td>
<td>最新结束值</td>
</tr>
</tbody>
</table>
<h4>reset()</h4>
<p>重置</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>-</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h4>pauseResume()</h4>
<p>切换渐变</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>-</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
</u-article></template>

