<h4>多选列</h4>
<p>对于<code>type</code>类型为<code>selection</code>的表格列，可以控制<code>checkbox</code>的选择状态，传入<code>data</code>中每个对象中属性<code>selected</code>属性表示默认是否处于选中状态，<code>disabled</code>表示是否可选择</p>
<p>同时也可在表格列规定<code>headSelection</code>属性，来控制列表是否可以全选。</p>
