<h4>自定义排序方法</h4>
<p>同步方式自定义排序方法传入<code>sortMethod</code>属性即可，点击排序，异步获取后端数据需要传入<code>sortRemoteMethod</code>方法即可，点击排序会自动执行<code>sortRemoteMethod</code>方法，默认让某一列处于排序状态，需要在<code>u-old-table-view</code>上添加<code>defaultSort</code>属性即可</p>
