        <span class="hljs-tag">&lt;<span class="hljs-name">u-old-table-view-column</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"icon"</span> <span class="hljs-attr">expand-class</span>=<span class="hljs-string">"infoIcon"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"expand"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"listlogs"</span> <span class="hljs-attr">default-text</span>=<span class="hljs-string">""</span> <span class="hljs-attr">expand-strict</span> <span class="hljs-attr">expand-icon</span>=<span class="hljs-string">"up-down"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"expandContent"</span> <span class="hljs-attr">slot-scope</span>=<span class="hljs-string">"scope"</span>&gt;</span>
                <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
