                        <span class="hljs-string">'2018-06-19 14:52:49云主机开始创建...'</span>,
                        <span class="hljs-string">'2018-06-19 14:53:20云主机创建完成，云主机 UUID：05ab50b1-a981-492d-bfac-ebbbf94cea5e'</span>,
