<h2>TableView API</h2>
<h3>Attrs/Props</h3>
<h4>视图相关属性</h4>
<table>
<thead>
<tr>
<th>Attr/Prop</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>color</td>
<td>String</td>
<td></td>
<td>值为<code>'light'</code>的时，表格头背景是<code>#fff</code></td>
</tr>
<tr>
<td>visible</td>
<td>Boolean</td>
<td><code>true</code></td>
<td>表格是否可见</td>
</tr>
<tr>
<td>showHeader</td>
<td>Boolean</td>
<td><code>true</code></td>
<td>是否展示表格头</td>
</tr>
<tr>
<td>layout</td>
<td>String</td>
<td><code>'fixed'</code></td>
<td>表格的布局方式, 可选值: <code>'fixed'</code>, <code>'auto'</code></td>
</tr>
<tr>
<td>border</td>
<td>Boolean</td>
<td><code>'false'</code></td>
<td>是否展示表格边框</td>
</tr>
<tr>
<td>width</td>
<td>Integer/String</td>
<td></td>
<td>表格组件的宽度</td>
</tr>
<tr>
<td>height</td>
<td>Integer/String</td>
<td></td>
<td>表格组件的高度</td>
</tr>
<tr>
<td>maxHeight</td>
<td>Integer/String</td>
<td></td>
<td>表格组件的最大高度</td>
</tr>
<tr>
<td>minHeight</td>
<td>Integer/String</td>
<td></td>
<td>表格组件的最小高度</td>
</tr>
<tr>
<td>radioTextField</td>
<td>String</td>
<td><code>'radioText'</code></td>
<td>单选按钮 文案字段key</td>
</tr>
<tr>
<td>radioValueField</td>
<td>String/Number</td>
<td><code>'radioLabel'</code></td>
<td>单选按钮值key</td>
</tr>
<tr>
<td>defaultText</td>
<td>String</td>
<td><code>'-'</code></td>
<td>默认当单元格取值为空时，默认显示的内容，此处是设置整个表格</td>
</tr>
<tr>
<td>loading</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否展示加载中的状态信息</td>
</tr>
<tr>
<td>loadText</td>
<td>String</td>
<td><code>''</code></td>
<td>加载中的文字信息提示</td>
</tr>
<tr>
<td>noDataText</td>
<td>String</td>
<td><code>''</code></td>
<td>当<code>data</code>属性为空数组时，展示的信息</td>
</tr>
<tr>
<td>pattern</td>
<td>String</td>
<td><code>'normal'</code></td>
<td>值设置为<code>'limit'</code>可支持显示指定数目的数据，可选值: <code>'normal'</code>, <code>'limit'</code></td>
</tr>
<tr>
<td>limit</td>
<td>String, Number</td>
<td><code>5</code></td>
<td>在<code>pattern</code>属性值为<code>'limit'</code>时，默认显示数据的数目</td>
</tr>
<tr>
<td>limitText</td>
<td>String</td>
<td><code>'查看更多'</code></td>
<td>在<code>pattern</code>属性值为<code>'limit'</code>时，数据的数目大于<code>'limit'</code>属性 的值时默认在表格最后一行显示的提示内容</td>
</tr>
<tr>
<td>allText</td>
<td>String</td>
<td><code>'收起'</code></td>
<td>在<code>pattern</code>属性值为<code>'limit'</code>时，显示所有数据后默认在表格最后一行显示的提示内容</td>
</tr>
<tr>
<td>expandPattern</td>
<td>String</td>
<td><code>'toggle'</code></td>
<td>规定<code>type</code>属性值为<code>'expand'</code>列的展开行为，可选值: <code>'toggle'</code>，<code>'normal'</code>。值为<code>'toggle'</code>时，展开一行后其他行将收回。 值为<code>'normal'</code>时，每行都可以展开。</td>
</tr>
<tr>
<td>rowClassName</td>
<td>Function</td>
<td></td>
<td>给表格行添加自定义class函数，第一个参数表示索引，即在第几行中，第二个参数是表格当前行数据</td>
</tr>
<tr>
<td>xScroll</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>鼠标滚动时表格是否可以横向滚动</td>
</tr>
<tr>
<td>showColor</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>单选选中行是否显示背景颜色</td>
</tr>
</tbody>
</table>
<h4>数据相关属性</h4>
<table>
<thead>
<tr>
<th>Attr/Prop</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>String</td>
<td></td>
<td>表格的标题</td>
</tr>
<tr>
<td>data</td>
<td>Array</td>
<td></td>
<td>表格默认要显示的数据</td>
</tr>
<tr>
<td>dataPattern</td>
<td>String</td>
<td><code>'copy'</code></td>
<td>默认数据是否采用深拷贝模式，默认是采取的，在大数据量展示情形中请将此值置为<code>'normal'</code></td>
</tr>
<tr>
<td>allChecked.sync</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>默认是否全部选中</td>
</tr>
<tr>
<td>defaultSort</td>
<td>Object&lt; title, order &gt;</td>
<td></td>
<td>默认的排序列和顺序值，其中<code>title</code>属性指定默认排序的列。<code>order</code>指定默认排序的顺序，可选值: <code>'desc'</code>,<code>'asc'</code>。</td>
</tr>
<tr>
<td>defaultFilter</td>
<td>Object&lt; title, value, column &gt;</td>
<td></td>
<td>默认采用某列进行过滤，其中<code>title</code>属性指定默认过滤的列，<code>value</code>指定默认过滤的值，在存在多个过滤列的时候可以使用此属性指定，当前只有一个列的时候可以不指定，默认会使用第一个filter列</td>
</tr>
<tr>
<td>forceFilter</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>数据发生变化时，存在数据过滤列，是否需要进行过滤，默认是不需要的</td>
</tr>
<tr>
<td>forceSort</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>数据发生变化时，存在数据排序，是否需要进行排序，默认是不需要的</td>
</tr>
<tr>
<td>sortMethod</td>
<td>Function</td>
<td></td>
<td>自定义排序方法，第一个参数为该列前一行数据，第二个参数为该列后一行数据，方法需要返回值，返回类型为<code>Boolean</code></td>
</tr>
<tr>
<td>sortRemoteMethod</td>
<td>Function</td>
<td></td>
<td>异步执行排序传入的方法，第一个参数是列字段，第二个参数是排序顺序，第三个参数是列对象</td>
</tr>
<tr>
<td>filterMethod</td>
<td>Function</td>
<td></td>
<td>自定义过滤方法，第一个参数为该列数据，第二个参数为列实例</td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<h4>(default)</h4>
<p>插入<code>&lt;u-old-table-view-column&gt;</code>子组件。</p>
<h4>title</h4>
<p>自定义表格标题</p>
<h4>limit-text</h4>
<p>在<code>pattern</code>属性值为<code>'limit'</code>时，数据的数目大于<code>'limit'</code>属性 的值时默认在表格最后一行显示的提示内容</p>
<h4>all-text</h4>
<p>在<code>pattern</code>属性值为<code>'limit'</code>时，显示所有数据后默认在表格最后一行显示的提示内容</p>
<h4>no-data-text</h4>
<p>数据为空时自定义显示文本</p>
<h4>expandIcon</h4>
<p>在列中<code>type</code>属性值为<code>'expand'</code>时，替换默认的切换图标</p>
<h3>Events</h3>
<h4>@sort-change</h4>
<p>点击排序标签触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.column</td>
<td>Object</td>
<td>当前列<code>column</code>的实例，含有当前列的所有信息，实质是<code>table-view-column</code>实例</td>
</tr>
<tr>
<td>$event.label</td>
<td>String</td>
<td>当前列的标签值</td>
</tr>
<tr>
<td>$event.order</td>
<td>String</td>
<td>当前列排序值： <code>'asc'</code>或<code>'desc'</code></td>
</tr>
</tbody>
</table>
<h4>@filter-change</h4>
<p>点击过滤标签触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.column</td>
<td>Object</td>
<td>当前列<code>column</code>的实例，含有当前列的所有信息，实质是<code>table-view-column</code>实例</td>
</tr>
<tr>
<td>$event.value</td>
<td>String</td>
<td>选中的标签值</td>
</tr>
<tr>
<td>$event.index</td>
<td>Number</td>
<td>当前列的索引值</td>
</tr>
</tbody>
</table>
<h4>@selection-change</h4>
<p>点击checkbox触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event</td>
<td>Array</td>
<td>选中的行的数据集合</td>
</tr>
</tbody>
</table>
<h4>@radio-change</h4>
<p>点击radio触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.value</td>
<td>String</td>
<td>选中的行的单选值</td>
</tr>
<tr>
<td>$event.row</td>
<td>Object</td>
<td>选中的行的值</td>
</tr>
<tr>
<td>$event.index</td>
<td>Number</td>
<td>选中的行的索引</td>
</tr>
</tbody>
</table>
<h4>@row-click</h4>
<p>点击表格行触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.data</td>
<td>Object</td>
<td>选中的行的数据集合</td>
</tr>
<tr>
<td>$event.index</td>
<td>Int</td>
<td>行数据所在的索引值</td>
</tr>
</tbody>
</table>
<h4>@toggle-expand</h4>
<p>列展开或收回时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.index</td>
<td>Int</td>
<td>选中的行的数据索引</td>
</tr>
<tr>
<td>$event.direction</td>
<td>String</td>
<td>icon的方向，向哪个方向展开或收起</td>
</tr>
<tr>
<td>$event.row</td>
<td>Object</td>
<td>选中行的数据</td>
</tr>
</tbody>
</table>
<h4>@mouseenter</h4>
<p>表格某行数据上的鼠标处于mouseenter</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.index</td>
<td>Int</td>
<td>行数据索引</td>
</tr>
</tbody>
</table>
<h4>@mouseleave</h4>
<p>表格某行数据鼠标处于mouseleave</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.index</td>
<td>Int</td>
<td>行数据索引</td>
</tr>
</tbody>
</table>
<h2>TableViewColumn API</h2>
<h3>Props/Attrs</h3>
<h4>视图相关属性</h4>
<table>
<thead>
<tr>
<th>Prop/Attr</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>width</td>
<td>String</td>
<td></td>
<td>是指列的宽度值</td>
</tr>
<tr>
<td>border</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否有边框，默认无</td>
</tr>
<tr>
<td>fixed</td>
<td>String</td>
<td></td>
<td>将列固定在左边或右边，参见例子<code>固定左右列</code>，可选值：<code>'left'</code>,<code>'right'</code>,<code>''</code></td>
</tr>
<tr>
<td>ellipsis</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否换行，默认换行，值为<code>true</code>则开启不换行，超出部分显示为省略号</td>
</tr>
<tr>
<td>defaultText</td>
<td>String</td>
<td><code>'-'</code></td>
<td>默认当单元格取值为空时，默认显示的内容，此处是设置某一列的显示</td>
</tr>
<tr>
<td>headClass</td>
<td>String</td>
<td><code>''</code></td>
<td>给表格头部<code>'th'</code>添表格内容过多加自定义<code>'class'</code>名称，方便对表格头部自定义样式</td>
</tr>
<tr>
<td>placement</td>
<td>String</td>
<td><code>'bottom-start'</code></td>
<td>可过滤列选项弹出层的弹出方向，可选值：<code>'top'</code>, <code>'bottom'</code>, <code>'left'</code>, <code>'right'</code>, <code>'top-start'</code>, <code>'top-end'</code>, <code>'bottom-start'</code>, <code>'bottom-end'</code>, <code>'left-start'</code>,<code>'left-end'</code>, <code>'right-start'</code>, <code>'right-end'</code></td>
</tr>
<tr>
<td>expandIcon</td>
<td>String</td>
<td><code>'right-down'</code></td>
<td><code>icon</code>的图标展开方向，提供两种类型，一种是默认向右点击向下<code>'right-down'</code>，另一种是默认向下点击向上<code>'up-down'</code></td>
</tr>
<tr>
<td>expandStrict</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>当<code>type</code>属性值为<code>'expand'</code>时，开启<code>'expand'</code>严格匹配模式， 只有对应的<code>label</code>字段有值才显示<code>icon</code></td>
</tr>
<tr>
<td>expandLabel</td>
<td>String</td>
<td></td>
<td>当<code>type</code>属性值为<code>'expand'</code>时，当出现组合形式的时候，使用此字段指定<code>icon</code>展开依赖的属性字段</td>
</tr>
<tr>
<td>expandClass</td>
<td>String</td>
<td></td>
<td>当<code>type</code>属性值为<code>'expand'</code>时，定义<code>icon</code>的样式</td>
</tr>
<tr>
<td>visible</td>
<td>Boolean</td>
<td><code>true</code></td>
<td>控制列的显示/隐藏</td>
</tr>
<tr>
<td>horizontal</td>
<td>String</td>
<td>``</td>
<td>控制列的对齐方式 默认是左对齐</td>
</tr>
<tr>
<td>columnClass</td>
<td>String</td>
<td>``</td>
<td>自定义列样式,给某一列添加class</td>
</tr>
</tbody>
</table>
<h4>数据相关属性</h4>
<table>
<thead>
<tr>
<th>Prop/Attr</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>String</td>
<td></td>
<td>列的标题</td>
</tr>
<tr>
<td>value</td>
<td>String</td>
<td></td>
<td>默认过滤项选中的值</td>
</tr>
<tr>
<td>label</td>
<td>String</td>
<td></td>
<td>对象data中对象的属性</td>
</tr>
<tr>
<td>type</td>
<td>String</td>
<td></td>
<td>可选值：<code>'selection'</code>,<code>'expand'</code>,<code>'time'</code>。值为<code>'selection'</code>,表示此列是否是可选择的， 对于日期类型的值可以设置值为<code>'time'</code>,配合<code>timeFormat</code>属性，转换成想要的日期格式, <code>'expand'</code>表示在当前行出现icon标识，点击icon当前行下新增新一行数据</td>
</tr>
<tr>
<td>timeFormat</td>
<td>String</td>
<td><code>'YYYY-MM-DD HH:mm:ss'</code></td>
<td>定义<code>type</code>属性值为<code>'time'</code>时，返回的指定日期格式的值</td>
</tr>
<tr>
<td>sortable</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>列是否可排序</td>
</tr>
<tr>
<td>filter</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>列是否可过滤</td>
</tr>
<tr>
<td>headSelection</td>
<td>Boolean</td>
<td><code>true</code></td>
<td>列表是否可全选</td>
</tr>
<tr>
<td>options</td>
<td>Array{name, value}</td>
<td></td>
<td>过滤项列表</td>
</tr>
<tr>
<td>formatter</td>
<td>Function</td>
<td></td>
<td>自定义格式化列数据，第一个参数是含有该行数据的对象，第二个参数是列实例</td>
</tr>
<tr>
<td>sortMethod（准备废弃，提升到父元素上）</td>
<td>Function</td>
<td></td>
<td>自定义排序方法，第一个参数为该列前一行数据，第二个参数为该列后一行数据，方法需要返回值，返回类型为<code>Boolean</code></td>
</tr>
<tr>
<td>sortRemoteMethod（准备废弃，提升到父元素上）</td>
<td>Function</td>
<td></td>
<td>异步执行排序传入的方法，第一个参数是列字段，第二个参数是排序顺序，第三个参数是列对象</td>
</tr>
<tr>
<td>filterMethod（准备废弃，提升到父元素上）</td>
<td>Function</td>
<td></td>
<td>自定义过滤方法，第一个参数为该列数据，第二个参数为列实例</td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<h4>headerTitle</h4>
<p>插入自定义<code>th</code>标签内容。</p>
<h4>expandContent</h4>
<p>插入自定义<code>icon</code>展开的内容。</p>
